.desktop-menu {
    -ms-flex: 0 0 400px;
        flex: 0 0 400px;
}

.desktop-menu li {
    display: inline-block;
}

.desktop-menu button {
    outline: none;
    border: none;
    background: none;
    color: white;
    font-size: 16px;
    margin-left: 25px;
    margin-right: 25px;
}

.desktop-menu button:hover {
    cursor: pointer;
}

.desktop-menu a {
    margin-left: 25px;
   
}

.desktop-menu hr {
    width: 50px;
    height: 2px;
    border-color: turquoise;
    margin-top: 15px;
    position: relative;
    left: 25px;
    -webkit-transition: all 0.5s ease;
    -o-transition: all 0.5s ease;
    transition: all 0.5s ease;
}

.desktop-menu .projects {
    left: 125px;
    width: 67px;
    -webkit-transition: all 0.5s ease;
    -o-transition: all 0.5s ease;
    transition: all 0.5s ease;
}


