div.about {
    width: 60%;
    margin-left: 20%;
    margin-top: 12.5%;
    margin-bottom: 12.5%;
    text-align: center;
}

div.about.hide {
    display: none;
}

@media screen and (max-width: 1000px) {
    .about {
        width: 90%;
        margin-left: 5%;
    }
}

.about p {
    font-weight: lighter;
    font-size: 2em;
    line-height: 1.5em;
}

.about p span {
    font-weight: 500;
}


