.mobile-menu .menuButton {
    position: absolute;
    right: 1em;
    top: 1em;
    z-index: 1001;
    color: white;
    font-size: 2em;
    background: none;
    border: none;
    outline: none;
    -webkit-transition: all .2s cubic-bezier(0, 1.26, .8, 1.28);
    -o-transition: all .2s cubic-bezier(0, 1.26, .8, 1.28);
    transition: all .2s cubic-bezier(0, 1.26, .8, 1.28);
}

.mobile-menu .menuButton.show {
    -webkit-transform: scale(1.2, 1.2);
        -ms-transform: scale(1.2, 1.2);
            transform: scale(1.2, 1.2);
    cursor: pointer;
}

.mobile-menu .menu {
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.97);
    position: fixed;
    top: 0;
    right: 0;
    -webkit-transition: all 300ms ease-in;
    -o-transition: all 300ms ease-in;
    transition: all 300ms ease-in;
    z-index: 1000;
}

.mobile-menu .menu.hide {
    -webkit-transform: translate3d(0, -100vh, 0);
            transform: translate3d(0, -100vh, 0);
}

.mobile-menu .menu.show {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
}

.mobile-menu .menu {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

.mobile-menu li:first-of-type {
    margin-top: 6em;
}

.mobile-menu li {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: center;
        justify-content: center;
}

.mobile-menu li.social a {
    width: 100px;
    padding: 0.75em;
    font-size: 30px;
    text-align: center;
}

.mobile-menu .menu button {
    padding: 1em;
    font-size: 20px;
    color: white;
    outline: none;
    border: none;
    background: none;
    width: 100%;
}

.mobile-menu .menu a {
    padding: 1em;
    font-size: 20px;
    width: 100%;
    text-align: center;
}

.mobile-menu .menu a:active {
    outline: 1px solid turquoise;
}

.mobile-menu .menu button:active {
    outline: 1px solid turquoise;
}

.mobile-menu p.hide {
    display: none;
}

.mobile-menu p.cross {
    position: relative;
    right: 3px;
}

.mobile-menu .logo {
    padding: 0;
    margin: 0;
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}


