div.projects {
    width: 960px;
    margin: 0 auto;
}

div.projects .repos {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
}

@media screen and (max-width: 1000px) {
    div.projects {
        width: 640px;
    }

    div.projects .repos {
        grid-template-columns: 1fr 1fr;
    }
}

@media screen and (max-width: 700px) {
    div.projects {
        width: 330px;
    }

    div.projects .repos {
        grid-template-columns: 1fr;
    }
}

div.projects.hide {
    display: none;
}

.projects > h2 {
    margin-left: 10px;
    margin-bottom: 20px;
}

.projects .repo {
    position: relative;
    width: 310px;
    margin: 5px;
    padding: 1em;
    background: rgba(0, 0, 0, 0.5);
}

.projects .repo-bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0.03;
    background: url("../../../images/logo.svg");
    background-size: 70%;
    background-repeat: no-repeat;
    background-position: right center;
}

.projects .repo-name {
    margin-bottom: 0.25em;
    color: white;
}

.projects .repo-desc {
    font-size: 14px;
    color: rgb(184, 184, 184);
    line-height: 20px;
}

.projects .repo-lang {
    color: rgb(102, 102, 102);
    margin-bottom: 1em;
}

.projects .repo-pushed {
    font-size: 15px;
}

.projects .repo::before, .projects .repo::after {
    -webkit-box-sizing: inherit;
            box-sizing: inherit;
    content: '';
    position: absolute;
    border-bottom: 2px solid transparent;
    width: 0;
    height: 0;
  }

.projects .repo::before {
    top: 0;
    left: 0;
}

.projects .repo::after {
    bottom: 0;
    right: 0;
}

.projects .repo:hover::before, 
.projects .repo:hover::after {
    width: 100%;
    height: 100%;
}

.projects .repo:hover::after {
    border-bottom-color: turquoise;
    -webkit-transition:
        border-color 0s ease-out,
        width 0.5s ease-out,
        height 0.5s ease-out;
    -o-transition:
        border-color 0s ease-out,
        width 0.5s ease-out,
        height 0.5s ease-out;
    transition:
        border-color 0s ease-out,
        width 0.5s ease-out,
        height 0.5s ease-out;
}


