footer {
    width: 305px;
    margin: 0 auto;
    padding-bottom: 2em;
    padding-top: 2em;
}

@media screen and (max-width: 1000px) {
    footer {
        display: none;
    }
}

footer ul {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
}

footer ul li {
    display: inline-block;
    font-size: 30px;
}

footer ul li a {
    padding: 20px 20px;
}

footer ul li a:hover {
    border-bottom: 1px solid turquoise;
}

footer img {
    width: 48px;
    height: 48px;
    margin: 0 auto;
    display: block;
    margin-top: 2em;
    margin-bottom: 1em;
    opacity: 0.2;
}


